<template>
  <div class="content">
    <div class="addressList">
      <van-radio-group v-model="radio" v-for="(item ,index) in 3" :key="index">
        <div class="addressNew">
          <div class="list">
            <div class="user">
              <div class="name">小橘子</div>
              <div class="phone">134****8759</div>
            </div>
            <div class="address">广东省 广州市 海珠区 艺影街147号丽影华庭A区第7座1606</div>
          </div>
          <div class="listOne">
            <van-radio :name="index" checked-color="#F2A94A">设置为默认地址</van-radio>
            <div class="icon">
              <div class="operationImg operationImgOne" @click="toAddressEqit">
                <img src="@/assets/personal/binji.png" alt />
              </div>
              <div class="operationImg" @click="isShow">
                <img src="@/assets/personal/del.png" alt />
              </div>
            </div>
          </div>
        </div>
      </van-radio-group>
    </div>
    <van-dialog v-model="isDel" show-cancel-button confirm-button-color="#ff4a54">
      <img src="@/assets/all/tips.png" alt />
      <p>确认删除该商品吗？</p>
    </van-dialog>
    <div class="add" @click="toAddressEqit">添加新地址</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      noData: false,
      radio: 0,
      isDel: false //删除地址提示
    };
  },
  methods: {
    toAddressEqit() {
      this.$router.push({
        name: "addressEqit"
      });
    },
    isShow() {
      this.isDel = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  .addressList {
    font-size: 28px;
    width: 100%;
    display: inline-table;
    box-sizing: border-box;
    .addressNew {
      width: 100%;
      display: inline-table;
      margin-bottom: 16px;
      background: white;
      .list {
        box-sizing: border-box;
        text-align: left;
        margin: 30px 24px 0;
        padding-bottom: 12px;
        
        border-bottom: thin solid #E5E5E5;
        .user {
          width: 100%;
          margin-bottom: 18px;
          display: flex;
          color: #222;
          .name{
            margin-right: 50px;
          }
         
          .phone {
            font-size: 26px;
            float: right;
            margin-right: 8px;
          }
        }
        .address {
          width: 100%;
          color: #555;
          line-height: 46px;
        }
      }
     
      .listOne {
        height: 88px;
        box-sizing: border-box;
        position: relative;
        width: 100%;
        display: flex;
        .van-radio {
          margin-left: 24px;
          // line-height: 80px;
        }
        .icon {
          position: absolute;
          right: 24px;
          top: 28px;

          .operationImg {
            width: 32px;
            height: 34px;
            float: left;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .operationImgOne {
            width: 34px;
            margin-right: 40px;
          }
        }
      }
    }
  }
  .van-dialog {
    width: 580px;
    img {
      display: block;
      width: 72px;
      height: 72px;
      margin: 48px auto 20px;
    }
    p {
      text-align: center;
      font-size: 30px;
      line-height: 42px;
      color: #555;
      margin-bottom: 48px;
    }
  }
  .add{
    margin: 100px 44px;
    text-align: center;
    height: 80px;
    background:linear-gradient(90deg,rgba(255,74,84,1) 0%,rgba(253,101,109,1) 100%);
    border-radius:10px;
    line-height:80px;
    color: #fff;
    font-size:30px;
  }
}
</style>


